	module.exports = {"data":{"markdownRemark":{"html":"<p>この頃からチビッコ属性の片鱗が出てきていたことをうかがわせられる1枚。左から “リーン”、“キュラン”、“アーク” という名前。 ちなみにこの頃描いていた漫画はどれも酷い鬱全開なストーリーだった。遊び盛りの子供が人殺しして(強要させられて)、その罪悪感に耐え切れず何度も戻しては泣き叫び、終いには崖から飛び降りてそれでも死ねずそのうち泣き疲れて……と、そんなシーンがあったりとか。</p>","frontmatter":{"cover":null,"date":"1998-12-01T00:00:00+09:00","strDate":"1998/11/30","title":"ガキんちょ"}}},"pathContext":{"path":"/ja/blog/1998/12/01-boys/","langKey":"ja"}}

